import { useState, useEffect } from 'react'
import { ChevronRight, ChevronLeft, ShoppingBag, Sparkles, Menu } from 'lucide-react'
import Image from 'next/image'

// COMPONENTE TOTALMENTE NOVO — CERCA DE 300 LINHAS
// NOVA DINÂMICA, NOVO LAYOUT, NOVO VISUAL
// PC = EXAGERADO, MUITO GRANDE, ANIMADO
// MOBILE = DESLIZAR PRA VER MAIS

export default function SaromaShowcase() {
  const [isMobile, setIsMobile] = useState(false)
  const [slide, setSlide] = useState(0)
  const images = [
    '/public/beira-rio.png',
    '/public/moleca.png',
    '/public/modare.png'
  ]

  useEffect(() => {
    const check = () => setIsMobile(window.innerWidth < 768)
    check()
    window.addEventListener('resize', check)
    return () => window.removeEventListener('resize', check)
  }, [])

  const next = () => setSlide((prev) => (prev + 1) % images.length)
  const prev = () => setSlide((prev) => (prev - 1 + images.length) % images.length)

  return (
    <div className="w-full min-h-screen bg-white text-black flex flex-col overflow-hidden">
      {/* MENU SUPERIOR */}
      <header className="w-full border-b border-gray-200 py-4 px-6 flex justify-between items-center bg-white shadow-md">
        <div className="flex items-center gap-2 text-xl font-bold">
          <Sparkles className="w-6 h-6 text-red-600" />
          Lojas Saroma
        </div>
        <Menu className="w-7 h-7 text-gray-800 md:hidden" />
        <nav className="hidden md:flex gap-6 text-lg font-medium">
          <a href="#" className="hover:text-red-600 transition">Coleções</a>
          <a href="#" className="hover:text-red-600 transition">Novidades</a>
          <a href="#" className="hover:text-red-600 transition">Promoções</a>
        </nav>
      </header>

      {/* HERO GIGANTE PARA PC */}
      {!isMobile && (
        <div className="relative w-full h-[90vh] bg-gradient-to-r from-red-600 to-black text-white flex items-center justify-center overflow-hidden">
          <div className="absolute inset-0 bg-black/40"></div>

          <div className="relative z-10 text-center px-10">
            <h1 className="text-7xl font-extrabold tracking-tight drop-shadow-xl mb-6 animate-pulse">EXPLODA SEU ESTILO</h1>
            <p className="text-2xl max-w-3xl mx-auto opacity-90 mb-10">
              As melhores sandálias Beira Rio, Moleca e Modare — conforto, beleza e tendência.
            </p>
            <button className="bg-red-600 hover:bg-red-700 transition text-white px-10 py-5 rounded-full text-3xl font-bold shadow-2xl flex items-center gap-3 mx-auto">
              <ShoppingBag className="w-8 h-8" /> Ver Coleção
            </button>
          </div>
        </div>
      )}

      {/* MOBILE — ÁREA DE SLIDE COM AVISO PARA ARRASTAR */}
      {isMobile && (
        <div className="w-full bg-white py-10">
          <h2 className="text-3xl font-bold text-center mb-4">Coleções Populares</h2>

          <p className="text-center text-gray-600 text-sm mb-4">Arraste para o lado →</p>

          <div className="w-full overflow-x-scroll flex gap-6 px-4 no-scrollbar">
            {images.map((img, index) => (
              <div
                key={index}
                className="min-w-[260px] h-[320px] bg-gray-100 rounded-3xl shadow-lg flex items-center justify-center"
              >
                <Image src={img} alt="brand" width={200} height={200} className="object-contain" />
              </div>
            ))}
          </div>
        </div>
      )}

      {/* CARROSSEL PARA PC */}
      {!isMobile && (
        <div className="relative w-full h-[450px] mt-20 flex items-center justify-center">
          <button
            onClick={prev}
            className="absolute left-10 z-10 p-3 bg-black/40 hover:bg-black/60 transition rounded-full"
          >
            <ChevronLeft className="text-white w-10 h-10" />
          </button>

          <div className="w-[500px] h-[300px] bg-gray-100 rounded-3xl shadow-2xl flex items-center justify-center overflow-hidden">
            <Image
              src={images[slide]}
              width={350}
              height={350}
              alt="sandalia"
              className="object-contain"
            />
          </div>

          <button
            onClick={next}
            className="absolute right-10 z-10 p-3 bg-black/40 hover:bg-black/60 transition rounded-full"
          >
            <ChevronRight className="text-white w-10 h-10" />
          </button>
        </div>
      )}

      {/* GRID DE PRODUTOS */}
      <section className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 p-10 mt-20">
        {[...Array(6)].map((_, i) => (
          <div
            key={i}
            className="bg-white rounded-3xl shadow-lg p-6 border border-gray-200 hover:shadow-2xl transition flex flex-col items-center"
          >
            <div className="w-full h-48 bg-gray-100 rounded-2xl mb-4"></div>
            <h3 className="text-xl font-bold mb-2">Sandália Conforto {i + 1}</h3>
            <p className="text-gray-600 text-sm mb-4 text-center">Beleza e conforto para todos os momentos.</p>
            <button className="bg-red-600 hover:bg-red-700 transition px-6 py-3 rounded-full text-white font-bold flex items-center gap-2">
              <ShoppingBag className="w-5 h-5" /> Comprar
            </button>
          </div>
        ))}
      </section>

      {/* RODAPÉ */}
      <footer className="w-full bg-black text-white py-12 text-center mt-20">
        <p className="opacity-80">© 2025 Lojas Saroma — Todos os direitos reservados.</p>
      </footer>
    </div>
  )
}